@font-face {
  font-family: Maven;
  src: url("../assets/fonts/font.ttf");
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
  font-family: Maven;
}
html {
  scroll-behavior: smooth;
}
body {
  background: #333;
  font-family: Maven;
  width: 100%;
  margin: 0;
  overflow-x: hidden;
}

img {
  pointer-events: none;
}

#btn_get_in_touch_footer:hover {
  color: aqua;
}

.btn-dark {
  padding: 0.9rem;
  margin-top: 1rem;
  background: #292b2c;
  color: white;
  border: none;
  font-size: 1rem;
  border-radius: 5px;
  font-family: Maven;
}

.btn-dark:hover {
  box-shadow: 0px 0px 0px 5px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
.nav-link:hover {
  color: #ffb700;
}
.btn {
  text-align: center;
  text-decoration: none;
  height: auto;
  justify-content: center;
  align-items: center;
  display: flex;
  width: max-content;
}

.clicked {
  color: #ffb700;
}

.hire {
  height: 2.5rem;
  font-family: Maven;
  font-size: 1.1rem;
  background: transparent;
  border: 2px solid #ffb700;
  color: #ffb700;
  border-radius: 5px;
  padding: 0 1rem;
}

.hire:hover {
  background: #ffb700;
  color: black;
  cursor: pointer;
}

