hbToolbar {
  display: flex;
  height: 4rem;
  width: 100%;
  color: white;
  background: #292b2c;
  padding: 1.2rem 1.5rem;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1000;
}

hbToolbar hbToggle {
  margin-left: auto;
  display: none;
}
.cwhite {
  color: white;
}
.p10 {
  padding: 10px;
}

hbToolbar hbToggle :hover {
  cursor: pointer;
}

hbToolbar hbToggle button {
  background: transparent;
  padding: 0.2rem 1rem;
  font-size: 1.8rem;
  color: white;
  border: none;
  transition: 400ms;
}

hbMenu {
  margin-left: auto;
  width: 40rem;
  transition: 0.4s;
}

hbMenu ul {
  margin-right: 2rem;
  float: right;
  margin-bottom: 0;
}

hbMenu ul li {
  display: inline-block;
}

hbMenu ul li a {
  color: white;
  position: relative;
  padding: 0.5rem;
  font-size: 1.3rem;
  border-radius: 10px;
}

hbText {
  font-weight: 400;
  font-size: 1.5rem;
  font-family: Maven;
}

hbContact {
  padding: 2rem;
  display: block;
}

hbContact hr {
  height: 1px;
  background: #333;
  border: none;
  margin-top: 1rem;
}

hbContact .input {
  height: 3.5rem;
  margin-top: 2rem;
  width: 100%;
  outline: none;
  font-size: 1rem;
  font-family: Maven;
  padding: 1rem;
  background: transparent;
  border: 1px solid #333;
  border-radius: 5px;
}

hbContact .txt {
  height: 7rem;
}

hbLocations {
  display: block;
  padding: 2rem;
  background: #ffb700;
}

hbLocations * {
  margin-top: 1rem;
}

hbLocations span {
  display: block;
}

hbLocations hr {
  height: 0px;
  border: none;
  margin-top: 1.9rem;
}

hbFooter {
  display: block;
  background: #333333;
  color: white;
  padding: 1rem;
  margin: auto;
  text-align: center;
}

#dashboard {
  padding-top: 150px;
  padding-bottom: 150px;
  padding-left: 10px;
  padding-right: 10px;
  color: rgb(197, 197, 197);
  height: 52vh;
}

#dashboard pre {
  justify-content: flex-start;
  align-items: center;
  display: flex;
  color: #ffb700;
  font-size: large;
  font-weight: bold;
  width: max-content;
}
#dashboard pre span {
  font-size: 14px;
  white-space: nowrap;
  border-spacing: 8px;
  overflow: hidden;
  color: azure;
  animation: blink 800ms steps(30) infinite normal,
    typewriter 2s steps(30) infinite alternate;
  border-right: 3px solid #ffb700;
}

.blink {
  animation: blnk 800ms infinite normal;
  font-weight: bold;
}
@keyframes typewriter {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
@keyframes blink {
  from {
    border-color: #ffb700;
  }
  to {
    border-color: transparent;
  }
}

@keyframes blnk {
  0% {
    color: #ffb700;
  }
  90% {
    color: transparent;
  }
}

#dashboard .slide-wrapper {
  display: flex;
  width: 100%;
}

#dashboard .slide-root {
  position: relative;
}

#dashboard .slide-root .slide-top {
  position: absolute;
  color: #e7e7e7;
  font-weight: bold;
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 36%, 0% 66%);
  clip-path: polygon(0% 0%, 100% 0%, 100% 36%, 0% 66%);
  top: 0;
  left: 0;
  -webkit-animation: slide 8s infinite;
  animation: slide 8s infinite;
}

#dashboard .slide-root .slide-bottom-wrapper {
  position: absolute;
  font-weight: bold;
  top: 0;
  left: 0;
}

#dashboard .slide-root .slide-bottom {
  color: #ffb700;
  -webkit-clip-path: polygon(0% 66%, 100% 36%, 100% 100%, 0% 100%);
  clip-path: polygon(0% 66%, 100% 36%, 100% 100%, 0% 100%);
  display: inline;
  font-weight: bold;
}

@keyframes slide {
  0% {
    transform: translateX(0);
  }
  8% {
    transform: translate(-20px, 2%);
  }
  40% {
    transform: translateX(0);
  }
}

@-webkit-keyframes slide {
  0% {
    transform: translateX(0);
  }
  8% {
    transform: translate(-20px, 2%);
  }
  40% {
    transform: translateX(0);
  }
}

#footer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 100%;
  background-color: rgb(37, 37, 37);
  width: 100%;
}
.footer_item {
  width: 100%;
  height: 100%;
  padding: 16px;
}
.center {
  text-align: center;
}
.wrap {
  width: max-content;
}
.find_us a {
  color: #ffb700;
  text-decoration: underline;
}

.find_us .mail {
  text-decoration: none;
  color: white;
}

.title .underline {
  background-color: tomato;
  width: 20px;
  height: 3px;
  margin-top: 8px;
  transition: 400ms;
}
.section:hover > .title > .underline {
  width: 250px;
}

.separator {
  display: block;
  position: relative;
}
.top-separator {
  position: absolute;
  background-color: rgb(172, 9, 9);
}
.bottom-separator {
  position: absolute;
  background-color: #dfd0ab;
}

bubles {
  border-radius: 100%;
  position: fixed;
  pointer-events: none;
  background: #20e3a2;
  box-shadow: 10px 10px 30px #31c8a2, -10px -10px 30px #31c8a2;
  transform: translate(-50%, -50%);
  animation: colorgen 1s infinite;
}

@keyframes colorgen {
  0% {
    opacity: 1;
    -webkit-filter: hue-rotate(0deg);
    filter: hue-rotate(0deg);
  }
  100% {
    opacity: 0;
    -webkit-filter: hue-rotate(270deg);
    filter: hue-rotate(270deg);
  }
}

/* product animation style start here */


.item-wrapper {
  width: 100%;
  position: relative;
  height: 300px;
  overflow: hidden;
  border-radius: 16px;
}

.item {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: -45%;
  transition: all 800ms;
  background-color: transform;
  overflow: visible;
}

.item .post-left {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #3b3b3b;
  width: 100%;
  height: 100%;
  clip-path: polygon(0% 50%, 50% 100%, 0% 100%);
}

.item .post-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  right: 0;
  background-color: #3b3b3b;
  width: 100%;
  height: 100%;
  clip-path: polygon(0% 100%, 50% 100%, 100% 100%);
}

.item .post-right {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-color: #3b3b3b;
  width: 100%;
  height: 100%;
  clip-path: polygon(100% 50%, 50% 100%, 100% 100%);
}

.item .post-top {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-color: #ffb700;
  width: 100%;
  height: 100%;
  clip-path: polygon(0% 50%, 50% 100%, 100% 50%);
  z-index: -1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.item .title {
  position: absolute;
  z-index: 5;
  top: 55%;
  left: 0%;
  right: 0%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  text-align: center;
  color: #333;
  font-size: 1.3rem;
  transition: all 1s;
}
.item .title pre {
  align-self: center;
  color: #333;
  width: max-content;
  padding: 4px;
  border-radius: 8px;
}

.item .imgwrapper {
  width: 100%;
  height: 100%;
  top: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  color: red;
  text-align: center;
  transition: all 1s;
  position: absolute;
  overflow: visible;
}

.item .imgwrapper img {
  width: 110%;
}

.item .imgwrapper .show-item-details {
  top: 40%;
  left: 0;
  right: 0;
  padding: 4px;
  position: absolute;
}
.item .imgwrapper .show-item-details a {
  background-color: #ffb700;
  border: 1px solid rgb(0, 0, 0);
  color: #000000;
  border-radius: 8px;
  font-size: 1.15rem;
  padding: 8px;
  text-decoration: none;
  transition: all 500ms;
  cursor: pointer;
}
.item .imgwrapper .show-item-details a:hover {
  background-color: black;
  color: #ffb700;
  border: 1px solid rgb(0, 0, 0);
  font-size: 1.3rem;
  padding: 8px;
  text-decoration: none;
  cursor: pointer;
}

/* logo flag animation start  */

.item .imgwrapper .logo-flag-wrapper {
  position: absolute;
  top: 100%;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  transition: all 2.5s;
}
.item .imgwrapper .logo-flag-wrapper .flag-stand {
  width: 10px;
  height: 100%;
  background: linear-gradient(to left, #ffb700a0, black);
}
.item .imgwrapper .logo-flag-wrapper .flag {
  position: relative;
  width: 100px;
  height: 60px;
  border: 2px solid #ffb70033;
  background-color: #00000037;

  -webkit-animation-name: shakewave;
  -webkit-animation-duration: 10s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  overflow: hidden;
}
.item .imgwrapper .logo-flag-wrapper .flag .waving {
  position: absolute;
  top: -3px;
  left: -3px;
  width: 106px;
  height: 66px;
  background-image: -webkit-gradient(
      linear,
      0% 30%,
      50% top,
      color-stop(0%, rgba(204, 99, 40, 0)),
      color-stop(40%, rgba(204, 99, 40, 0)),
      color-stop(90%, rgba(255, 255, 255, 0.16)),
      color-stop(100%, rgba(204, 99, 40, 0))
    ),
    -webkit-gradient(linear, 50% 30%, 100% top, color-stop(0%, rgba(204, 99, 40, 0)), color-stop(25%, rgba(255, 255, 255, 0.181)), color-stop(50%, rgba(204, 99, 40, 0)), color-stop(80%, rgba(255, 255, 255, 0.125)), color-stop(100%, rgba(204, 99, 40, 0)));

  background-size: 200%;
  background-position: right;

  -webkit-animation-name: flagwave;
  -webkit-animation-duration: 25s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
}
@-webkit-keyframes shakewave {
  0% {
    border-top-right-radius: 10%;
    border-bottom-right-radius: 0%;
  }

  25% {
    border-top-right-radius: 20%;
    border-bottom-right-radius: 0%;
  }
  50% {
    border-top-right-radius: 5%;
    border-bottom-right-radius: 10%;
  }
  75% {
    border-top-right-radius: 0%;
    border-bottom-right-radius: 20%;
  }
  100% {
    border-top-right-radius: 0%;
    border-bottom-right-radius: 0%;
  }
}
@-webkit-keyframes flagwave {
  0% {
    background-position: -400px 0px, -400px 0px, -400px 0px, -400px 0px;
  }

  100% {
    background-position: 800px 0px, 800px 0px, 800px 0px, 800px 0px;
  }
}

.item .imgwrapper .logo-flag-wrapper .flag .flag-inner img {
  width: 60%;
  height: 100%;
  padding: 4px;
  align-self: center;
  justify-self: center;
  border-radius: 50%;
}

/* logo flag part end here  */

.item .details-card {
  transition: all 2s;
  position: absolute;
  display: flex;
  color: white;
  width: 100%;
  align-self: center;
  height: max-content;
  text-align: center;
  bottom: 0;
  top: 78%;
  padding: 0 30px 0 30px;
  overflow-wrap: normal;
  word-wrap: normal;
  z-index: 99;
}

.details-card .details-card-inner-wrapper {
  padding: 8px;
  word-wrap: break-word;
  align-self: center;
  width: 100%;
  height: 100%;
  max-lines: 1;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.808);
}

.details-card .details-card-inner-wrapper img {
  border-radius: 1.9rem;
  padding: 2px;
  border: 2px solid #ffb700;
  width: 3.8rem;
  height: 3.8rem;
}

.post-bottom-container {
  position: absolute;
  top: 0%;
  width: 100%;
  height: 100%;
  background-color: #3b3b3b;
  transition: all 1s;
  z-index: 5;
}
.post-bottom-container .bottom-right a {
  text-decoration: none;
  color: #ffb700;
}
.bottom-right {
  position: absolute;
  top: 30%;
  bottom: 0;
  right: 5%;
  z-index: 6;
  font-size: large;
  color: white;
}

.product-item:hover > .item-wrapper > .item {
  top: 0%;
}

.product-item:hover > .item-wrapper > .item > .post-top {
  background-color: #626262;
  clip-path: polygon(0% 50%, 50% 25%, 100% 50%);
}
.product-item:hover > .item-wrapper > .item > .post-left {
  clip-path: polygon(0% 50%, 50% 75%, 0% 100%);
}
.product-item:hover > .item-wrapper > .item > .post-right {
  clip-path: polygon(100% 50%, 50% 75%, 100% 100%);
}
.product-item:hover > .item-wrapper > .item > .post-bottom {
  clip-path: polygon(0% 100%, 50% 75%, 100% 100%);
}
.product-item:hover > .item-wrapper > .item > .title {
  visibility: hidden;
  color: transparent;
  left: 0;
  right: 0;
  top: 0;
  width: 1px;
  height: 1px;
}
.product-item:hover > .item-wrapper > .item > .title > pre {
  color: #333;
  background-color: #333;
}
.product-item:hover > .item-wrapper > .item > .imgwrapper {
  width: 100%;
  height: 100%;
  top: 0%;
  color: transparent;
}

.product-item:hover
  > .item-wrapper
  > .item
  > .imgwrapper
  > .post-bottom-container {
  top: 100%;
}
.product-item:hover > .item-wrapper > .item > .details-card {
  top: 60%;
  padding: 4px;
  transition: all 2s;
}
.product-item:hover
  > .item-wrapper
  > .item
  > .details-card
  > .details-card-inner-wrapper
  > img {
  height: 0px;
  width: 0px;
  margin: 0px;
  padding: 0px;
  transition: all 1s;
}

.product-item:hover > .item-wrapper > .item > .imgwrapper > .logo-flag-wrapper {
  top: 10%;
  left: 0;
  transition: all 2.5s;
}

/* product item animation style end here  */

#background {
  width: 100%;
  height: 52%;
  position: fixed;
  z-index: -10;
  background-color: black;
}

.section {
  background-color: #333;
}
.section-wrapper {
  background-color: #333;
  width: 100%;
}

@media screen and (max-width: 860px) {
  hbMenu ul {
    text-align: center;
  }

  hbMenu ul li a {
    background: transparent;
  }

  hbMenu ul li {
    display: block;
    margin-top: 1.5rem;
  }

  hbMenu {
    position: fixed;
    top: 4rem;
    left: 0;
    width: 100%;
    height: 55%;
    display: flex;
    justify-content: center;
    background: #444444;
    transform: translateY(-150%);
    z-index: -999;
    padding-bottom: 0.5rem;
  }

  hbToolbar hbToggle {
    display: block;
  }

  .hire {
    height: 3.5rem;
    width: max-content;
    font-family: Maven;
    font-size: 1.1rem;
    background: transparent;
    border: 2px solid #ffb700;
    color: #ffb700;
    border-radius: 5px;
  }
  #footer {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    height: 100%;
    background-color: rgb(37, 37, 37);
    width: 100%;
  }
  .footer_item {
    height: 100%;
    width: 100%;
    height: 100%;
    padding: 20px;
  }
  .center {
    text-align: center;
  }
}
